











import { Component, Prop, Vue } from 'vue-property-decorator'
import { IArticleData  } from '../../../api/types'
import { getlikesList } from '../../../api/actions'
import articleCard from '@/components/article/index.vue'
import empty from '@/components/emptyBox/index.vue'
interface Irouter {
  path: string;
  query: {
      articleId: number;
  }
}
@Component({
  components: {
    articleCard,
    empty
  }
})
export default class extends Vue {
  private likeList: IArticleData[] = []
  private loading: boolean = false
  private noMore: boolean = false
  private isEmpty: boolean = false
  private page: number = 0

  private onLoad() {
    this.page = this.page + 1
    this.fetchData()
  }
  // 用户点赞的
  private async fetchData() {
    this.loading = true
    const { data } = await getlikesList({author: this.$route.query.author, page: this.page})
    if (data.length > 0) {
      this.loading = false
      this.likeList = this.likeList.concat(data)
    } else {
      this.isEmpty = true
    }
    if (data.length < 10) {
      this.noMore = true
      this.loading = false
    }
  }
  created () {
    this.fetchData()
  }
}
